import { BodyText1 } from 'components/Text/Body';
import { useFocusOnMount } from 'hooks/useFocusOnMount';
import { useThrottledFn } from 'hooks/useThrottledFn';
import { buildAddressString } from 'pages/AddressSimple/AddressSimple.tools';
import React from 'react';
import { addressSubmitAction } from 'store/actions/server';
import { focusBorderMixin } from 'style/mixin/focus';
import styled from 'styled-components';
import { useSelector } from 'hooks/useSelector';
import { useDispatch } from 'hooks/useDispatch';
import { addressSelectSuggestItemAction } from 'store/actions/addressSimplePage';
import { color } from 'style/const/color';

const StyledRoot = styled.div`
    position: relative;
    padding: 36px 32px;
    box-sizing: border-box;
    border-radius: 24px;

    background: ${color.surface.liquid1};

    ${focusBorderMixin({ borderRadius: 24 })}
`;

const StyledContent = styled(BodyText1)`
    color: ${color.text.white};

    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
`;

interface SuggestListItemProps {
    id: string;
    shouldFocusOnMount?: boolean;
}
export const AddressSimpleSuggestListItem: React.FC<SuggestListItemProps> = ({ id, shouldFocusOnMount }) => {
    const suggest = useSelector(({ twoGisSuggestsData: { itemsMap } }) => itemsMap[id]);
    const rootRef = React.useRef<HTMLDivElement>(null);
    const onMountRef = useFocusOnMount<HTMLDivElement>();
    const ref = shouldFocusOnMount ? onMountRef : rootRef;
    const dispatch = useDispatch();
    const handleClick = useThrottledFn(
        () => {
            dispatch(addressSubmitAction());
        },
        200,
        undefined,
        [dispatch],
    );
    const handleFocus = React.useCallback(() => {
        dispatch(addressSelectSuggestItemAction(id));
    }, [dispatch, id]);

    const addressName = suggest && buildAddressString(suggest);

    return (
        <StyledRoot ref={ref} tabIndex={0} onClick={handleClick} onFocus={handleFocus}>
            <StyledContent>{addressName}</StyledContent>
        </StyledRoot>
    );
};
