import { push, replace, goBack } from 'connected-react-router';
import {
    addressGuideAction,
    addressInputAction,
    addressInputAddAction,
    addressInputDeclineAction,
    goToCatalogAction,
    goToPromo,
    validationOkAction,
} from 'store/actions/native';
import { goBackToStart } from 'store/actions/router';
import { AppThunkAction } from 'store/constants';
import {
    AddressInputAddPayload,
    AddressInputPayload,
    GoToCatalogCommandPayload,
    GoToPromoCommandPayload,
    ValidationOkCommandPayload,
} from 'store/constants/native';
import { ROUTES } from 'store/constants/router';
import { selectPathname } from 'store/selectors';
import { addressFullSuccessThunk } from 'store/thunks/addressFullPage';

export const goBackToStartAndReplaceThunk = (route: ROUTES): AppThunkAction => (dispatch) => {
    dispatch(goBackToStart());
    // TODO: сделать нормально https://sbtatlas.sigma.sbrf.ru/jira/browse/FOODTECHUI-223
    // setTimeout(() => {
    dispatch(replace(route));
    // }, 100);
};

export const goToCatalogThunk = (payload: GoToCatalogCommandPayload): AppThunkAction => (dispatch) => {
    dispatch(goToCatalogAction(payload));

    // чтобы при перезагрузке страницы постоянно не переходить в каталог
    if (process.env.REACT_APP_ENVIRONMENT === 'development') {
        return;
    }

    dispatch(goBackToStartAndReplaceThunk(ROUTES.CATALOG));
};

export const goToPromoThunk = (payload: GoToPromoCommandPayload): AppThunkAction => (dispatch) => {
    dispatch(goToPromo(payload));
    dispatch(replace(ROUTES.PROMO));
};

export const validationOkThunk = (payload: ValidationOkCommandPayload): AppThunkAction => (dispatch) => {
    dispatch(validationOkAction(payload));

    dispatch(goBackToStartAndReplaceThunk(ROUTES.STATUS));
};

export const addressInputThunk = (payload: AddressInputPayload): AppThunkAction => (dispatch) => {
    dispatch(addressInputAction(payload));
    dispatch(push(ROUTES.ADDRESS_SIMPLE_QUESTION));
};

export const addressGuideThunk = (): AppThunkAction => (dispatch) => {
    dispatch(addressGuideAction());
    dispatch(push(ROUTES.ADDRESS_SIMPLE_GUIDE));
};

export const addressInputDeclineThunk = (): AppThunkAction => (dispatch, getState) => {
    const {
        addressSimplePage: { suggests },
    } = getState();
    const totalSuggestsCount = suggests.length;

    dispatch(addressInputDeclineAction());

    if (totalSuggestsCount > 1) {
        dispatch(replace(ROUTES.ADDRESS_SIMPLE));
    } else {
        dispatch(goBack());
    }
};

export const addressInputAddThunk = (payload: AddressInputAddPayload): AppThunkAction => (dispatch) => {
    dispatch(addressInputAddAction(payload));
    dispatch(push(ROUTES.ADDRESS_SIMPLE_QUESTION));
};

export const goToValidationThunk = (): AppThunkAction => (dispatch, getState) => {
    const state = getState();
    const currentPathname = selectPathname(state);

    switch (currentPathname) {
        // это значит что ввод доп. адреса сохранился успешно
        case ROUTES.ADDRESS_FULL_ALL: {
            dispatch(addressFullSuccessThunk());
            break;
        }

        default: {
            dispatch(push(ROUTES.CART));
            break;
        }
    }
};
