import { push } from 'connected-react-router';
import { GradientFade } from 'components/GradientFade/GradientFade';
import { BodyText1 } from 'components/Text/Body';
import { FootnoteText1 } from 'components/Text/Footnote';
import { HeadlineText1 } from 'components/Text/Headline';
import { useDispatch } from 'hooks/useDispatch';
import { useFocusOnMount } from 'hooks/useFocusOnMount';
import { useSelector } from 'hooks/useSelector';
import { useThrottledFn } from 'hooks/useThrottledFn';
import React from 'react';
import { addressShowKeyboardHintAction } from 'store/actions/addressSimplePage';
import { ROUTES } from 'store/constants/router';
import { color } from 'style/const/color';
import styled from 'styled-components';
import { ReactComponent as PlasmaBallSvg } from 'pages/AddressSimpleGuide/AddressSimpleGuide.assets/plasma-ball.svg';

const StyledRoot = styled.div`
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
`;

const circleContainerSize = 316;
const plasmaIconSize = 48;
const offsetToCenteringIcon = circleContainerSize / 2 - plasmaIconSize / 2;
const StyledPlasmaIcon = styled(PlasmaBallSvg)`
    position: absolute;
    height: ${plasmaIconSize}px;
    width: ${plasmaIconSize}px;
    top: ${offsetToCenteringIcon}px;
    left: ${offsetToCenteringIcon}px;
`;

export const StyledCircles = styled.div`
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    height: ${circleContainerSize}px;
    width: ${circleContainerSize}px;
    margin-top: 106px;

    &::after {
        position: absolute;
        top: 86px;
        left: 86px;
        right: 86px;
        bottom: 86px;

        content: '';
        border-radius: 50%;

        opacity: 0.3;
        background: rgba(255, 255, 255, 0.06);
        box-shadow: inset 0 4px 4px rgba(255, 255, 255, 0.15);
        backdrop-filter: blur(48px);
    }

    &::before {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;

        content: '';
        border-radius: 50%;

        opacity: 0.3;
        background: rgba(255, 255, 255, 0.02);
        box-shadow: inset 0 4px 4px rgba(255, 255, 255, 0.15);
        backdrop-filter: blur(48px);
    }
`;

const StyledTitle = styled(BodyText1)`
    margin-top: -54px;
    color: ${color.text.primary};
`;

const StyledSource = styled(HeadlineText1)`
    position: relative;
    margin-top: 32px;

    background: linear-gradient(to right, #00ffdf, #00ff5a);
    color: transparent;
    -webkit-background-clip: text;
`;

const StyledSubtitle = styled(BodyText1)`
    margin-top: 130px;
    color: ${color.text.secondary};
`;

const StyledOkButton = styled(FootnoteText1)`
    position: relative;
    display: inline-flex;
    height: 78px;
    width: 78px;
    justify-content: center;
    align-items: center;
    color: ${color.text.primary};
    margin-left: 12px;

    &::before {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;

        content: '';
        border-radius: 50%;
        z-index: -1;

        opacity: 0.3;
        background: ${color.surface.liquid1};
        box-shadow: inset 0 4px 4px rgba(255, 255, 255, 0.15);
        backdrop-filter: blur(48px);
    }
`;

export const AddressSimpleGuide: React.FC = () => {
    const isKeyboardHintVisible = useSelector(({ addressSimplePage }) => addressSimplePage.isKeyboardHintVisible);
    const okButtonRef = useFocusOnMount<HTMLDivElement>();
    const dispatch = useDispatch();
    const handleOkClick = useThrottledFn(
        () => {
            dispatch(push(ROUTES.ADDRESS_SIMPLE_INPUT));
        },
        200,
        undefined,
        [dispatch],
    );

    React.useEffect(() => {
        return () => {
            if (!isKeyboardHintVisible) {
                dispatch(addressShowKeyboardHintAction());
            }
        };
    }, [dispatch, isKeyboardHintVisible]);

    return (
        <StyledRoot>
            <GradientFade sizePx={50} isVisible kind="bottom">
                <StyledCircles>
                    <StyledPlasmaIcon />
                </StyledCircles>
            </GradientFade>
            <StyledTitle>Нажмите кнопку на пульте и назовите</StyledTitle>
            <StyledSource>улицу и дом</StyledSource>
            {isKeyboardHintVisible ? (
                <StyledSubtitle>
                    Чтобы ввести с клавиатуры, нажмите на пульте кнопку
                    <StyledOkButton ref={okButtonRef} tabIndex={0} onClick={handleOkClick}>
                        ОК
                    </StyledOkButton>
                </StyledSubtitle>
            ) : null}
        </StyledRoot>
    );
};
