import { StoreUser } from 'store/constants/user';
import { AppReducer, AppState } from 'store/constants';
import { memoize } from 'tools/memoize';

const memoizedPrepareSuggestItems = memoize((itemsMap: AppState['twoGisSuggestsData']['itemsMap']) =>
    Object.keys(itemsMap),
);

// важно сохранить ссылку чтобы хуки внутри компонента отрабатывали нормально
const prepareSuggestItems = (state: AppState) => memoizedPrepareSuggestItems(state.twoGisSuggestsData.itemsMap);

export const getHasValidAddress = (user: StoreUser) => {
    const { street, house, city, address, addressLocationLatitude, addressLocationLongitude } = user;

    return street && house && city && address && addressLocationLatitude && addressLocationLongitude;
};

export const addressSimplePageView: AppReducer = (state, action) => {
    const { addressSimplePage } = state;

    switch (action.type) {
        case 'address/SELECT_SUGGEST_ITEM': {
            addressSimplePage.suggestsListIndex = state.addressSimplePage.suggests.findIndex(
                (itemId) => itemId === action.payload,
            );

            break;
        }

        case 'ADDRESS_INPUT': {
            addressSimplePage.inputValue = action.payload.input;

            break;
        }

        case 'ADDRESS_INPUT_ADD': {
            addressSimplePage.inputValue = action.payload.input;

            break;
        }

        case 'address/CHANGE_INPUT_VALUE': {
            addressSimplePage.inputValue = action.payload;

            break;
        }

        case 'address/SHOW_KEYBOARD_HINT': {
            addressSimplePage.isKeyboardHintVisible = true;

            break;
        }

        default:
            break;
    }

    addressSimplePage.suggests = prepareSuggestItems(state);
};
